.counter {
  font-size: 2rem;
  font-weight: 600;
  color: #0a2b1e;
}

.counter__title {
  font-size: 1rem;
  color: #0a2b1e;
  font-weight: 500;
}

.about__content {
  padding-left: 50px;
}

.about__img img {
  border-radius: 15px;
}

.about__content h2 {
  font-weight: 600;
}
.blockquote{
  /* max-width: 600px; */
  margin: 10px 30px;
  background: #f8fff8;
  border: 1px solid #b0d2cb;
  color: rgb(109, 81, 81);
}
.blockquote__text{
  font-family: 'Gelasio',serif;
  font-style: italic;
  font-size: 1.15rem;
  line-height: 1.5;
  margin: 0;
  padding: 10px;

}
.blockquote--quoted::before{
  content:  "\201c";
  font-size: 80px;
  color: #009578;
  display: block;
  margin-top: -20px;
  margin-bottom: -50px;
}
.blockquote__border{
  border-left-width: 10px;
}
.blockquote-brown{
  margin: 10px 30px;
  background: #d8cdc6;
  border: 1px solid #dce4e2;
  color: rgb(109, 81, 81);
  border: 1px solid #d7d7cb;
  border-left-width: 10px;
  border-right-width: 10px;
}
.counter{
  justify-content: center;
  text-align: center;
}
.headercenter{
  margin-left: 50px;
  text-align: center;
  justify-content: center;
}
.aimcontiner{
  padding: 200px;
}
.imgdiv{
  width: 50px;
  background-color: rgb(217, 237, 238);
}
.nameq{
  font-weight: bold;
  font-style: normal;
  text-align: right;
  margin-right: 50px;
}
