  /* padding: 15px; */
  text-align: center;
  color: rgb/(207, 24, 24);
  height: 100%;
  font-size: 50px;
  border-radius: 20px;
  /* margin-top: 20px; */
}
.block{
  /* background-color: rgb(226, 232, 235); */
